interface RadarChartProps {
  dimensions?: {
    operationalDrag: { norm: number }
    manualControls: { norm: number }
    fragmentation: { norm: number }
    insightLatency: { norm: number }
  }
}

export default function RadarChartPlaceholder({ dimensions }: RadarChartProps) {
  const scores = dimensions || {
    operationalDrag: { norm: 0 },
    manualControls: { norm: 0 },
    fragmentation: { norm: 0 },
    insightLatency: { norm: 0 },
  }

  const center = 250
  const maxRadius = 150
  const maxScore = 10
  const minVisibleRadius = 10

  const dimensions_list = [
    { name: 'Operational\nDrag', value: scores.operationalDrag.norm, angle: 0 },
    { name: 'Manual\nControls', value: scores.manualControls.norm, angle: 90 },
    { name: 'Fragmentation', value: scores.fragmentation.norm, angle: 180 },
    { name: 'Insight\nLatency', value: scores.insightLatency.norm, angle: 270 },
  ]

  const getPoint = (angle: number, radius: number) => {
    const rad = (angle * Math.PI) / 180
    return {
      x: center + radius * Math.cos(rad - Math.PI / 2),
      y: center + radius * Math.sin(rad - Math.PI / 2),
    }
  }

  const getRadius = (value: number) => {
    const clamped = Math.max(0, Math.min(maxScore, value || 0))
    if (clamped === 0) {
      return minVisibleRadius
    }
    return (clamped / maxScore) * maxRadius
  }

  const polygonPoints = dimensions_list
    .map((d) => {
      const radius = getRadius(d.value)
      const point = getPoint(d.angle, radius)
      return `${point.x},${point.y}`
    })
    .join(' ')

  const gridLines = []
  for (let i = 1; i <= 5; i++) {
    const radius = (i / 5) * maxRadius
    const points = dimensions_list
      .map((d) => {
        const point = getPoint(d.angle, radius)
        return `${point.x},${point.y}`
      })
      .join(' ')
    gridLines.push(
      <polygon
        key={`grid-${i}`}
        points={points}
        fill="none"
        stroke="#e2e8f0"
        strokeWidth="1"
      />
    )
  }

  const axes = dimensions_list.map((d, i) => {
    const outerPoint = getPoint(d.angle, maxRadius)
    return (
      <line
        key={`axis-${i}`}
        x1={center}
        y1={center}
        x2={outerPoint.x}
        y2={outerPoint.y}
        stroke="#cbd5e1"
        strokeWidth="1"
      />
    )
  })

  const labels = dimensions_list.map((d) => {
    const labelPoint = getPoint(d.angle, maxRadius + 60)
    const lines = d.name.split('\n')
    return (
      <g key={`label-${d.name}`}>
        {lines.map((line, i) => (
          <text
            key={`label-line-${i}`}
            x={labelPoint.x}
            y={labelPoint.y + i * 16}
            textAnchor="middle"
            dominantBaseline="middle"
            fontSize="14"
            fontWeight="600"
            fill="#1e293b"
          >
            {line}
          </text>
        ))}
      </g>
    )
  })

  return (
    <div
      style={{
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: '#f8fafc',
        border: '2px solid #e2e8f0',
        borderRadius: '1rem',
        padding: '2rem',
        aspectRatio: '1',
      }}
    >
      <svg
        style={{ width: '100%', height: '100%' }}
        viewBox="0 0 500 500"
        xmlns="http://www.w3.org/2000/svg"
        preserveAspectRatio="xMidYMid meet"
      >
        {gridLines}
        {axes}
        <polygon points={polygonPoints} fill="#14B8A6" fillOpacity="0.2" stroke="#14B8A6" strokeWidth="2" />
        {dimensions_list.map((d, i) => {
          const radius = getRadius(d.value)
          const point = getPoint(d.angle, radius)
          return (
            <circle
              key={`point-${i}`}
              cx={point.x}
              cy={point.y}
              r="4"
              fill="#14B8A6"
              stroke="#fff"
              strokeWidth="2"
            />
          )
        })}
        {labels}
      </svg>
    </div>
  )
}
