/**
 * Friction Index Scoring Model Configuration
 * Maps all 8 questions to their answer scores and dimension assignments
 */

export interface ScoringRule {
  [answer: string]: number
}

export interface QuestionConfig {
  id: string
  text: string
  dimension: 'operationalDrag' | 'manualControls' | 'fragmentation' | 'insightLatency'
  scoring: ScoringRule
}

export interface ScoringModelConfig {
  questions: QuestionConfig[]
  dimensions: {
    [key: string]: {
      name: string
      questionIds: string[]
      maxScore: number
    }
  }
}

