import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App'
import './index.css'

const rootElement = document.getElementById('financial-drag-root')

if (!rootElement) {
  throw new Error('Missing #financial-drag-root element')
}

const view = rootElement.getAttribute('data-view')
const sessionId = rootElement.getAttribute('data-session-id')
const resultsUrl = rootElement.getAttribute('data-results-url')

if (resultsUrl) {
  window.__FINANCIAL_DRAG_RESULTS_URL__ = resultsUrl
}

if (view === 'results' && sessionId) {
  const url = new URL(window.location.href)
  if (!url.searchParams.get('session')) {
    url.searchParams.set('session', sessionId)
    window.history.replaceState({}, '', url.toString())
  }
}

ReactDOM.createRoot(rootElement).render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
)
