interface ExportData {
  scoringResult: {
    index: number
    overallSeverity: string
    dimensions: {
      operationalDrag: { raw: number; norm: number }
      manualControls: { raw: number; norm: number }
      fragmentation: { raw: number; norm: number }
      insightLatency: { raw: number; norm: number }
    }
  }
  narrativeResult: {
    narrative?: string
    oneLiners?: {
      operationalDrag?: string
      manualControls?: string
      fragmentation?: string
      insightLatency?: string
    }
    implications?: string[]
  } | null
}

type JsPdfCtor = new (options?: {
  orientation?: 'portrait' | 'landscape'
  unit?: 'pt'
  format?: 'a4'
}) => any

const COLORS = {
  ink: [15, 23, 42] as const,
  muted: [71, 85, 105] as const,
  border: [226, 232, 240] as const,
  accent: [20, 184, 166] as const,
  accentSoft: [236, 253, 245] as const,
  white: [255, 255, 255] as const,
}

function formatSeverity(value: string): string {
  if (!value) return 'Low'
  return value.charAt(0).toUpperCase() + value.slice(1)
}

function formatToday(): string {
  const now = new Date()
  const yyyy = now.getFullYear()
  const mm = String(now.getMonth() + 1).padStart(2, '0')
  const dd = String(now.getDate()).padStart(2, '0')
  return `${yyyy}-${mm}-${dd}`
}

function drawWrappedText(pdf: any, lines: string[], x: number, yStart: number, lineHeight: number) {
  let y = yStart
  lines.forEach((line: string) => {
    pdf.text(line, x, y)
    y += lineHeight
  })
  return y
}

function drawCenteredWrappedText(
  pdf: any,
  text: string,
  maxWidth: number,
  centerX: number,
  yStart: number,
  lineHeight: number
) {
  const lines = pdf.splitTextToSize(text, maxWidth)
  let y = yStart
  lines.forEach((line: string) => {
    pdf.text(line, centerX, y, { align: 'center' })
    y += lineHeight
  })
  return { lines, y }
}

function drawRadar(
  pdf: any,
  dimensions: ExportData['scoringResult']['dimensions'],
  centerX: number,
  centerY: number,
  maxRadius: number
) {
  const maxScore = 10
  const axis = [
    { label: 'Operational\nDrag', value: dimensions.operationalDrag.norm, angleDeg: -90 },
    { label: 'Manual\nControls', value: dimensions.manualControls.norm, angleDeg: 0 },
    { label: 'Fragmentation', value: dimensions.fragmentation.norm, angleDeg: 90 },
    { label: 'Insight\nLatency', value: dimensions.insightLatency.norm, angleDeg: 180 },
  ]

  const toPoint = (angleDeg: number, radius: number) => {
    const rad = (angleDeg * Math.PI) / 180
    return {
      x: centerX + radius * Math.cos(rad),
      y: centerY + radius * Math.sin(rad),
    }
  }

  pdf.setLineWidth(0.8)
  pdf.setDrawColor(...COLORS.border)
  for (let step = 1; step <= 6; step += 1) {
    const r = (step / 6) * maxRadius
    for (let i = 0; i < axis.length; i += 1) {
      const a = axis[i]
      const b = axis[(i + 1) % axis.length]
      const p1 = toPoint(a.angleDeg, r)
      const p2 = toPoint(b.angleDeg, r)
      pdf.line(p1.x, p1.y, p2.x, p2.y)
    }
  }

  axis.forEach((item) => {
    const p = toPoint(item.angleDeg, maxRadius)
    pdf.line(centerX, centerY, p.x, p.y)
  })

  pdf.setDrawColor(...COLORS.accent)
  pdf.setLineWidth(1.6)
  for (let i = 0; i < axis.length; i += 1) {
    const a = axis[i]
    const b = axis[(i + 1) % axis.length]
    const p1 = toPoint(a.angleDeg, (Math.max(0, Math.min(maxScore, a.value)) / maxScore) * maxRadius)
    const p2 = toPoint(b.angleDeg, (Math.max(0, Math.min(maxScore, b.value)) / maxScore) * maxRadius)
    pdf.line(p1.x, p1.y, p2.x, p2.y)
  }

  pdf.setFillColor(...COLORS.accent)
  axis.forEach((item) => {
    const p = toPoint(item.angleDeg, (Math.max(0, Math.min(maxScore, item.value)) / maxScore) * maxRadius)
    pdf.circle(p.x, p.y, 2.4, 'F')
  })

  pdf.setFont('helvetica', 'bold')
  pdf.setTextColor(...COLORS.ink)
  pdf.setFontSize(12)

  const topY = centerY - maxRadius - 34
  pdf.text('Operational', centerX, topY, { align: 'center' })
  pdf.text('Drag', centerX, topY + 14, { align: 'center' })

  pdf.text('Manual', centerX + maxRadius + 28, centerY + 4)
  pdf.text('Controls', centerX + maxRadius + 28, centerY + 18)

  const bottomY = centerY + maxRadius + 28
  pdf.text('Fragmentation', centerX, bottomY, { align: 'center' })

  pdf.text('Insight', centerX - maxRadius - 28, centerY + 4, { align: 'right' })
  pdf.text('Latency', centerX - maxRadius - 28, centerY + 18, { align: 'right' })
}

function drawPageBackground(pdf: any, pageWidth: number, pageHeight: number) {
  pdf.setFillColor(...COLORS.white)
  pdf.rect(0, 0, pageWidth, pageHeight, 'F')
  pdf.setFillColor(...COLORS.accent)
  pdf.rect(0, 0, pageWidth, 8, 'F')
}

async function toDataUrl(url: string): Promise<string | null> {
  try {
    const res = await fetch(url)
    if (!res.ok) return null
    const blob = await res.blob()
    return await new Promise((resolve) => {
      const reader = new FileReader()
      reader.onloadend = () => resolve(typeof reader.result === 'string' ? reader.result : null)
      reader.readAsDataURL(blob)
    })
  } catch {
    return null
  }
}

function drawDimensionCard(pdf: any, params: {
  x: number
  y: number
  width: number
  title: string
  scoreText: string
  detail: string
}) {
  const { x, y, width, title, scoreText, detail } = params
  const detailLines = detail ? pdf.splitTextToSize(detail, width - 24) : []
  const renderedDetail = detailLines.slice(0, 3)
  const lineHeight = 14
  const cardHeight = Math.max(84, 50 + renderedDetail.length * lineHeight)

  pdf.setDrawColor(...COLORS.border)
  pdf.roundedRect(x, y, width, cardHeight, 10, 10, 'S')

  pdf.setFont('helvetica', 'bold')
  pdf.setFontSize(12)
  pdf.setTextColor(...COLORS.ink)
  pdf.text(title, x + 14, y + 22)

  pdf.setFont('helvetica', 'bold')
  pdf.setFontSize(10)
  pdf.setTextColor(...COLORS.muted)
  pdf.text(scoreText, x + width - 14, y + 22, { align: 'right' })

  pdf.setFont('helvetica', 'normal')
  pdf.setFontSize(11)
  pdf.setTextColor(...COLORS.muted)
  drawWrappedText(pdf, renderedDetail, x + 14, y + 44, lineHeight)

  return cardHeight
}

export async function exportResultsPDF(data: ExportData): Promise<void> {
  try {
    // @ts-expect-error vendored UMD build has no TypeScript declarations
    const module = await import('../vendor/jspdf.umd.min.js')
    const jsPDF = (
      module.jsPDF ||
      module.default?.jsPDF ||
      (window as any).jspdf?.jsPDF
    ) as JsPdfCtor | undefined

    if (!jsPDF) {
      throw new Error('jsPDF runtime not available after loading vendor bundle.')
    }

    const pdf = new jsPDF({ orientation: 'portrait', unit: 'pt', format: 'a4' })

    const margin = 42
    const pageWidth = pdf.internal.pageSize.getWidth()
    const pageHeight = pdf.internal.pageSize.getHeight()
    const contentWidth = pageWidth - margin * 2

    // PAGE 1
    drawPageBackground(pdf, pageWidth, pageHeight)

    let y = 62
    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(46)
    pdf.setTextColor(...COLORS.ink)
    const hero = drawCenteredWrappedText(
      pdf,
      'Your close friction index',
      contentWidth - 24,
      pageWidth / 2,
      y,
      48
    )

    y = hero.y - 12
    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(16)
    pdf.setTextColor(...COLORS.muted)
    drawCenteredWrappedText(
      pdf,
      'A research-backed view of friction in your close process',
      contentWidth - 80,
      pageWidth / 2,
      y,
      18
    )

    // Score tile centered
    y += 38
    const scoreW = 132
    const scoreH = 132
    const scoreX = pageWidth / 2 - scoreW / 2
    pdf.setDrawColor(...COLORS.border)
    pdf.roundedRect(scoreX, y, scoreW, scoreH, 12, 12, 'S')
    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(56)
    pdf.setTextColor(...COLORS.ink)
    pdf.text(String(data.scoringResult.index), pageWidth / 2, y + 74, { align: 'center' })
    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(12)
    pdf.setTextColor(...COLORS.muted)
    pdf.text('OUT OF 100', pageWidth / 2, y + 102, { align: 'center' })

    // Radar card
    const radarCardY = y + scoreH + 30
    const radarCardH = 430
    pdf.setDrawColor(...COLORS.border)
    pdf.roundedRect(margin, radarCardY, contentWidth, radarCardH, 10, 10, 'S')
    drawRadar(pdf, data.scoringResult.dimensions, pageWidth / 2, radarCardY + 220, 108)

    // Force page break after the chart section.
    pdf.addPage()
    drawPageBackground(pdf, pageWidth, pageHeight)

    // Narrative card (always starts on new page)
    const narrativeY = 64
    const severityLabel = formatSeverity(data.scoringResult.overallSeverity)

    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(12)
    const narrativeLines = pdf.splitTextToSize(
      data.narrativeResult?.narrative || 'No narrative available for this report.',
      contentWidth - 56
    )
    const narrativeRendered = narrativeLines
    const narrativeCardH = Math.max(190, 116 + narrativeRendered.length * 16)

    pdf.setDrawColor(...COLORS.accent)
    pdf.setFillColor(...COLORS.accentSoft)
    pdf.roundedRect(margin, narrativeY, contentWidth, narrativeCardH, 12, 12, 'FD')

    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(22)
    pdf.setTextColor(...COLORS.ink)
    pdf.text('Your close process experiences', margin + 20, narrativeY + 34)
    pdf.setTextColor(...COLORS.accent)
    pdf.text(severityLabel, margin + 20, narrativeY + 56)
    const severityWidth = pdf.getTextWidth(severityLabel)
    pdf.setTextColor(...COLORS.ink)
    pdf.text(' friction', margin + 20 + severityWidth, narrativeY + 56)

    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(12)
    pdf.setTextColor(...COLORS.muted)
    drawWrappedText(pdf, narrativeRendered, margin + 20, narrativeY + 92, 16)

    // Continue below narrative on the same page unless pagination is needed.
    y = narrativeY + narrativeCardH + 64

    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(34)
    pdf.setTextColor(...COLORS.ink)
    pdf.text('Dimension breakdown', margin, y)
    y += 24

    const rows = [
      {
        title: 'Operational drag',
        scoreText: `Score: ${data.scoringResult.dimensions.operationalDrag.raw} (normalised ${data.scoringResult.dimensions.operationalDrag.norm})`,
        detail: data.narrativeResult?.oneLiners?.operationalDrag || '',
      },
      {
        title: 'Manual controls',
        scoreText: `Score: ${data.scoringResult.dimensions.manualControls.raw} (normalised ${data.scoringResult.dimensions.manualControls.norm})`,
        detail: data.narrativeResult?.oneLiners?.manualControls || '',
      },
      {
        title: 'Fragmentation',
        scoreText: `Score: ${data.scoringResult.dimensions.fragmentation.raw} (normalised ${data.scoringResult.dimensions.fragmentation.norm})`,
        detail: data.narrativeResult?.oneLiners?.fragmentation || '',
      },
      {
        title: 'Insight latency',
        scoreText: `Score: ${data.scoringResult.dimensions.insightLatency.raw} (normalised ${data.scoringResult.dimensions.insightLatency.norm})`,
        detail: data.narrativeResult?.oneLiners?.insightLatency || '',
      },
    ]

    rows.forEach((row) => {
      const previewCardHeight = Math.max(
        84,
        50 + Math.min(3, (row.detail ? pdf.splitTextToSize(row.detail, contentWidth - 24).length : 0)) * 14
      )
      if (y + previewCardHeight > pageHeight - 90) {
        pdf.addPage()
        drawPageBackground(pdf, pageWidth, pageHeight)
        y = 64
      }

      const cardHeight = drawDimensionCard(pdf, {
        x: margin,
        y,
        width: contentWidth,
        title: row.title,
        scoreText: row.scoreText,
        detail: row.detail,
      })
      y += cardHeight + 14
    })

    // Force "What this means..." to a new page.
    pdf.addPage()
    drawPageBackground(pdf, pageWidth, pageHeight)
    y = 64

    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(28)
    pdf.setTextColor(...COLORS.ink)
    pdf.text('What this means for your organisation', margin, y)
    y += 24

    const implications = data.narrativeResult?.implications || []
    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(12)
    pdf.setTextColor(...COLORS.muted)

    implications.forEach((item) => {
      const bulletLines = pdf.splitTextToSize(`- ${item}`, contentWidth - 6)
      const needed = bulletLines.length * 16 + 4
      if (y + needed > pageHeight - 44) {
        pdf.addPage()
        drawPageBackground(pdf, pageWidth, pageHeight)
        y = 64
      }
      y = drawWrappedText(pdf, bulletLines, margin, y, 16)
      y += 2
    })

    // Redwood automation section: keep on this page if space allows.
    const redwoodPreHeadingLines = pdf.splitTextToSize(
      'Where your friction is highest, Redwood delivers the most impact.',
      380
    )
    const redwoodCardHeadingLines = [
      'Redwood offers much higher levels of R2R',
      'automation',
    ]
    const redwoodSectionHeightEstimate =
      redwoodPreHeadingLines.length * 24 + 12 + 12 + 12 + 470 + 24
    const redwoodTopGap = 28
    if (y + redwoodTopGap + redwoodSectionHeightEstimate > pageHeight - 44) {
      pdf.addPage()
      drawPageBackground(pdf, pageWidth, pageHeight)
      y = 56
    } else {
      y += redwoodTopGap
    }

    // Section pre-header (matches web layout)
    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(18)
    pdf.setTextColor(...COLORS.ink)
    redwoodPreHeadingLines.forEach((line: string) => {
      pdf.text(line, pageWidth / 2, y, { align: 'center' })
      y += 24
    })

    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(11)
    pdf.setTextColor(...COLORS.muted)
    pdf.text('How Redwood transforms R2R performance', pageWidth / 2, y, { align: 'center' })
    y += 18

    pdf.setDrawColor(...COLORS.border)
    pdf.roundedRect(margin, y, contentWidth, 470, 12, 12, 'S')

    // Card heading + subtitle centered in the container
    let cardY = y + 34
    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(22)
    pdf.setTextColor(...COLORS.ink)
    redwoodCardHeadingLines.forEach((line: string) => {
      pdf.text(line, pageWidth / 2, cardY, { align: 'center' })
      cardY += 24
    })

    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(12)
    pdf.setTextColor(...COLORS.muted)
    pdf.text('Comprehensive automation comparison across Record-to-Report processes', pageWidth / 2, cardY, {
      align: 'center',
    })
    cardY += 14

    const automationImageUrl = new URL('../assets/chart.png', import.meta.url).href
    const automationImageData = await toDataUrl(automationImageUrl)
    if (automationImageData) {
      pdf.addImage(automationImageData, 'PNG', margin + 18, cardY + 8, contentWidth - 36, 180)
    } else {
      pdf.setFont('helvetica', 'italic')
      pdf.setFontSize(11)
      pdf.setTextColor(...COLORS.muted)
      pdf.text('Automation comparison image unavailable in this environment.', margin + 18, cardY + 22)
    }

    const statY = cardY + 204
    const statGap = 10
    const statW = (contentWidth - statGap * 3) / 4
    const stats = [
      { value: '80%', label: 'Task Management', sublabel: 'Redwood Automation' },
      { value: '70%', label: 'Journals & Accruals', sublabel: 'Redwood Automation' },
      { value: '80%', label: 'Intercompany', sublabel: 'Redwood Automation' },
      { value: '75%', label: 'Account Reconciliation', sublabel: 'Redwood Automation' },
    ]
    stats.forEach((stat, idx) => {
      const sx = margin + idx * (statW + statGap)
      pdf.setDrawColor(148, 163, 184)
      pdf.setFillColor(241, 245, 249)
      pdf.roundedRect(sx, statY, statW, 92, 8, 8, 'FD')
      pdf.setFont('helvetica', 'bold')
      pdf.setFontSize(18)
      pdf.setTextColor(...COLORS.muted)
      pdf.text(stat.value, sx + statW / 2, statY + 24, { align: 'center' })
      pdf.setFont('helvetica', 'bold')
      pdf.setFontSize(9)
      pdf.setTextColor(...COLORS.ink)
      const labelLines = pdf.splitTextToSize(stat.label, statW - 14)
      let ly = statY + 44
      labelLines.slice(0, 2).forEach((line: string) => {
        pdf.text(line, sx + statW / 2, ly, { align: 'center' })
        ly += 11
      })
      pdf.setFont('helvetica', 'normal')
      pdf.setFontSize(8.5)
      pdf.setTextColor(...COLORS.muted)
      const subLines = pdf.splitTextToSize(stat.sublabel, statW - 14)
      subLines.slice(0, 2).forEach((line: string) => {
        pdf.text(line, sx + statW / 2, ly, { align: 'center' })
        ly += 10
      })
    })

    pdf.setDrawColor(...COLORS.border)
    pdf.setFillColor(248, 250, 252)
    pdf.roundedRect(margin, statY + 106, contentWidth, 36, 8, 8, 'FD')
    pdf.setFont('helvetica', 'bold')
    pdf.setFontSize(10)
    pdf.setTextColor(...COLORS.ink)
    pdf.text('Average 76% automation vs 22.5% for non-Redwood solutions', margin + contentWidth / 2, statY + 128, {
      align: 'center',
    })

    // Footer on last page
    pdf.setFont('helvetica', 'normal')
    pdf.setFontSize(10)
    pdf.setTextColor(148, 163, 184)
    pdf.text(`Generated ${formatToday()}`, margin, pageHeight - 20)

    pdf.save(`close-friction-diagnostic-${formatToday()}.pdf`)
  } catch (error) {
    console.error('PDF export failed:', error)
    window.alert('PDF download failed. Please try again.')
  }
}
