import { useEffect, useCallback, useRef } from 'react'
import type { CompletionData } from '@/types'

export function isEmbedded(): boolean {
  try {
    return window.self !== window.top
  } catch {
    return true
  }
}

export function isEmbedMode(): boolean {
  const params = new URLSearchParams(window.location.search)
  return params.get('embed') === 'true' || isEmbedded()
}

export function useParentCommunication() {
  const resizeObserverRef = useRef<ResizeObserver | null>(null)
  const lastHeightRef = useRef<number>(0)
  const embedded = isEmbedMode()

  const sendToParent = useCallback((type: string, payload?: unknown) => {
    if (!embedded) return

    try {
      window.parent.postMessage(
        {
          type: `redwood:assessment:${type}`,
          payload,
        },
        '*'
      )
    } catch (error) {
      console.warn('[Assessment] Failed to send message to parent:', error)
    }
  }, [embedded])

  const notifyReady = useCallback(() => {
    sendToParent('ready')
  }, [sendToParent])

  const notifyComplete = useCallback((data: CompletionData) => {
    sendToParent('complete', data)
  }, [sendToParent])

  const requestResize = useCallback((height: number) => {
    if (Math.abs(height - lastHeightRef.current) > 10) {
      lastHeightRef.current = height
      sendToParent('resize', { height })
    }
  }, [sendToParent])

  const setupAutoResize = useCallback(() => {
    if (!embedded) return

    if (resizeObserverRef.current) {
      resizeObserverRef.current.disconnect()
    }

    const updateHeight = () => {
      const height = Math.max(
        document.body.scrollHeight,
        document.body.offsetHeight,
        document.documentElement.scrollHeight,
        document.documentElement.offsetHeight
      )
      requestResize(height)
    }

    resizeObserverRef.current = new ResizeObserver(() => {
      updateHeight()
    })

    resizeObserverRef.current.observe(document.body)

    const mutationObserver = new MutationObserver(() => {
      updateHeight()
    })

    mutationObserver.observe(document.body, {
      childList: true,
      subtree: true,
      attributes: true,
    })

    setTimeout(updateHeight, 100)

    return () => {
      resizeObserverRef.current?.disconnect()
      mutationObserver.disconnect()
    }
  }, [embedded, requestResize])

  useEffect(() => {
    const cleanup = setupAutoResize()
    return cleanup
  }, [setupAutoResize])

  useEffect(() => {
    if (embedded) {
      setTimeout(notifyReady, 50)
    }
  }, [embedded, notifyReady])

  return {
    isEmbedded: embedded,
    notifyReady,
    notifyComplete,
    requestResize,
  }
}
