import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { ChevronDown, ChevronUp } from 'lucide-react'
import { useState } from 'react'

interface StartupScreenProps {
  onStart: () => void
  currentStep: number
}

export function StartupScreen({ onStart }: StartupScreenProps) {
  const [isMethodologyExpanded, setIsMethodologyExpanded] = useState(false)

  return (
    <div className="min-h-screen bg-background">
      <div className="border-b border-border/40 sticky top-0 z-10 bg-background/95 backdrop-blur-md">
        <div className="max-w-[920px] mx-auto px-8 py-5">
          <div className="flex items-center justify-end">
            {/* Theme toggle placeholder */}
          </div>
        </div>
      </div>

      <div className="max-w-[920px] mx-auto px-8 py-20">
        <div className="text-center space-y-8 mb-20">
          <h1 className="text-6xl md:text-7xl font-bold text-foreground leading-[1.1] tracking-tight max-w-[840px] mx-auto">
            Reveal the hidden drag beneath your financial close
          </h1>
          <p className="text-xl md:text-2xl text-muted-foreground leading-relaxed max-w-[720px] mx-auto font-light">
            Most finance teams carry more manual friction, fragmentation and volatility than they realise. This
            diagnostic uncovers the three forces shaping your month-end performance.
          </p>
          <p className="text-sm text-muted-foreground/70 font-medium tracking-wide uppercase">
            Based on independent research from PwC, EY, Gartner, Hackett and ACCA
          </p>
        </div>

        <div className="flex justify-center mb-24">
          <Button
            onClick={onStart}
            data-testid="start-assessment"
            className="bg-primary hover:bg-primary/90 text-primary-foreground px-16 py-7 text-xl font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-200 hover:scale-[1.02]"
          >
            Start Assessment
          </Button>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-24">
          <Card className="p-8 hover:shadow-md transition-shadow duration-200 border-border/50">
            <h3 className="font-bold text-xl mb-4 text-foreground">Friction load</h3>
            <p className="text-base text-muted-foreground leading-relaxed">
              Rework, corrections and late adjustments that slow down the close.
            </p>
          </Card>
          <Card className="p-8 hover:shadow-md transition-shadow duration-200 border-border/50">
            <h3 className="font-bold text-xl mb-4 text-foreground">Fragmentation index</h3>
            <p className="text-base text-muted-foreground leading-relaxed">
              The hidden cost of spreadsheets, handoffs and disconnected systems.
            </p>
          </Card>
          <Card className="p-8 hover:shadow-md transition-shadow duration-200 border-border/50">
            <h3 className="font-bold text-xl mb-4 text-foreground">Stability profile</h3>
            <p className="text-base text-muted-foreground leading-relaxed">
              How predictable and resilient your month-end close is each cycle.
            </p>
          </Card>
        </div>

        <div className="max-w-[640px] mx-auto mb-24">
          <h3 className="text-3xl font-bold text-foreground mb-8 text-center">What to expect</h3>
          <div className="space-y-5">
            {[
              'Seven behavioural diagnostic signals',
              'No operational data required',
              'Takes under one minute',
              'Research-backed interpretation',
              'Clear explanation of diagnostic patterns',
            ].map((item, index) => (
              <div key={index} className="flex items-center gap-5 group">
                <div className="w-2.5 h-2.5 rounded-full bg-primary shrink-0 group-hover:scale-125 transition-transform" />
                <span className="text-lg text-foreground leading-relaxed font-normal">{item}</span>
              </div>
            ))}
          </div>
        </div>

        <Card className="p-8 border-border/50">
          <button
            onClick={() => setIsMethodologyExpanded(!isMethodologyExpanded)}
            className="w-full flex items-center justify-between text-left group"
          >
            <h3 className="text-xl font-bold text-foreground group-hover:text-redwood-primary transition-colors">
              Methodology and research sources
            </h3>
            {isMethodologyExpanded ? (
              <ChevronUp className="h-6 w-6 text-muted-foreground group-hover:text-redwood-primary transition-colors" />
            ) : (
              <ChevronDown className="h-6 w-6 text-muted-foreground group-hover:text-redwood-primary transition-colors" />
            )}
          </button>

          {!isMethodologyExpanded ? (
            <p className="text-base text-muted-foreground mt-4 leading-relaxed">
              This diagnostic draws on widely recognised research into close efficiency, spreadsheet risk and finance
              process performance.
            </p>
          ) : (
            <div className="mt-8 space-y-8 text-base">
              <div>
                <h4 className="font-bold text-foreground mb-4 text-lg">Friction load</h4>
                <div className="space-y-4 text-muted-foreground leading-relaxed">
                  <p>
                    Manual rework in the close is a consistent marker of upstream weakness.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: EY, Smart closing and reporting (2020) {' '}
                      <a href="https://www.ey.com" className="text-redwood-primary hover:underline">
                        https://www.ey.com
                      </a>
                    </span>
                  </p>
                  <p>
                    Top-performing finance teams minimise time spent on transactional corrections and rework.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: PwC, Finance effectiveness benchmarking (2024) {' '}
                      <a href="https://www.pwc.com" className="text-redwood-primary hover:underline">
                        https://www.pwc.com
                      </a>
                    </span>
                  </p>
                </div>
              </div>

              <div>
                <h4 className="font-bold text-foreground mb-4 text-lg">Fragmentation index</h4>
                <div className="space-y-4 text-muted-foreground leading-relaxed">
                  <p>
                    Spreadsheet reliance is strongly associated with inefficiency, error risk and rework loops.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: PwC and ACCA, Finance effectiveness survey (2012) {' '}
                      <a href="https://www.accaglobal.com" className="text-redwood-primary hover:underline">
                        https://www.accaglobal.com
                      </a>
                    </span>
                  </p>
                  <p>
                    Handoffs between disconnected systems increase reconciliation effort and introduce failure points.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: Hackett Group research summarised via Trintech insights (2021) {' '}
                      <a href="https://www.thehackettgroup.com" className="text-redwood-primary hover:underline">
                        https://www.thehackettgroup.com
                      </a>
                    </span>
                  </p>
                  <p>
                    Fragmented tools are a primary driver for adopting integrated close platforms.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: Gartner, Market guide for cloud financial close solutions (latest edition) {' '}
                      <a href="https://www.gartner.com" className="text-redwood-primary hover:underline">
                        https://www.gartner.com
                      </a>
                    </span>
                  </p>
                </div>
              </div>

              <div>
                <h4 className="font-bold text-foreground mb-4 text-lg">Stability profile</h4>
                <div className="space-y-4 text-muted-foreground leading-relaxed">
                  <p>
                    World-class finance teams operate with reduced process variability and faster, more reliable
                    insights.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: Hackett Group, Digital world class finance research (2025) {' '}
                      <a href="https://www.thehackettgroup.com" className="text-redwood-primary hover:underline">
                        https://www.thehackettgroup.com
                      </a>
                    </span>
                  </p>
                  <p>
                    Predictable closes correlate strongly with standardisation and automation, not individual heroics.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: IMA / Hackett R2R benchmark principles {' '}
                      <a href="https://www.imanet.org" className="text-redwood-primary hover:underline">
                        https://www.imanet.org
                      </a>
                    </span>
                  </p>
                </div>
              </div>

              <div>
                <h4 className="font-bold text-foreground mb-4 text-lg">Spreadsheet risk</h4>
                <div className="space-y-4 text-muted-foreground leading-relaxed">
                  <p>
                    Spreadsheets carry inherent risks due to lack of control and auditability.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: ACCA, Spreadsheet risk guidance {' '}
                      <a href="https://www.accaglobal.com" className="text-redwood-primary hover:underline">
                        https://www.accaglobal.com
                      </a>
                    </span>
                  </p>
                  <p>
                    Heavy spreadsheet use is linked to inefficiency and non-value-adding work.
                    <br />
                    <span className="text-sm mt-1 inline-block">
                      Source: PwCACCA joint survey {' '}
                      <a href="https://www.pwc.com" className="text-redwood-primary hover:underline">
                        https://www.pwc.com
                      </a>
                    </span>
                  </p>
                </div>
              </div>
            </div>
          )}
        </Card>
      </div>
    </div>
  )
}
