import { friScoringModel } from '@/config/friScoringModel'

function classifyLevel(normalizedScore: number): string {
  if (normalizedScore <= 3) return 'low'
  if (normalizedScore <= 6) return 'moderate'
  if (normalizedScore <= 8) return 'high'
  return 'severe'
}

export function classifyOverallSeverity(index: number): string {
  if (index <= 25) return 'low'
  if (index <= 50) return 'moderate'
  if (index <= 75) return 'high'
  return 'severe'
}

export function computeIndex(dimensionsRaw: {
  operationalDrag: number
  manualControls: number
  fragmentation: number
  insightLatency: number
}): number {
  const totalRaw = Object.values(dimensionsRaw).reduce((sum, score) => sum + score, 0)
  return Math.round((totalRaw / 120) * 100)
}

export function scoreAnswers(assessmentData: Record<string, string>) {
  const dimensions = {
    operationalDrag: { raw: 0, norm: 0, level: '' },
    manualControls: { raw: 0, norm: 0, level: '' },
    fragmentation: { raw: 0, norm: 0, level: '' },
    insightLatency: { raw: 0, norm: 0, level: '' },
  }

  friScoringModel.questions.forEach((question) => {
    const answer = assessmentData[question.id]
    if (answer !== undefined && question.scoring[answer] !== undefined) {
      const score = question.scoring[answer]
      const dimensionKey = question.dimension as keyof typeof dimensions
      dimensions[dimensionKey].raw += score
    }
  })

  Object.keys(dimensions).forEach((key) => {
    const dimensionKey = key as keyof typeof dimensions
    const normalized = Math.round(dimensions[dimensionKey].raw / 3)
    dimensions[dimensionKey].norm = normalized
    dimensions[dimensionKey].level = classifyLevel(normalized)
  })

  const dimensionsRaw = {
    operationalDrag: dimensions.operationalDrag.raw,
    manualControls: dimensions.manualControls.raw,
    fragmentation: dimensions.fragmentation.raw,
    insightLatency: dimensions.insightLatency.raw,
  }
  const index = computeIndex(dimensionsRaw)
  const overallSeverity = classifyOverallSeverity(index)

  return {
    dimensions,
    index,
    overallSeverity,
  }
}

export interface DimensionScore {
  raw: number
  norm: number
  level: string
}

export interface ScoringResult {
  dimensions: {
    operationalDrag: DimensionScore
    manualControls: DimensionScore
    fragmentation: DimensionScore
    insightLatency: DimensionScore
  }
  index: number
  overallSeverity: string
}
