import { useState } from 'react'
import { useLocation } from 'wouter'
import { AssessmentScreen } from '@/components/assessment-screen'
import { useParentCommunication } from '@/hooks/useParentCommunication'
import { scoreAnswers } from '@/utils/friScoringEngine'

function generateSessionId(): string {
  return Date.now().toString(36) + Math.random().toString(36).slice(2, 10)
}

export default function Assessment() {
  const [, setLocation] = useLocation()
  const [sessionId] = useState<string>(() => generateSessionId())
  const { notifyComplete } = useParentCommunication()
  const [assessmentData, setAssessmentData] = useState({
    q1_operational_drag: '',
    q2_delay_cause: '',
    q3_manual_percentage: '',
    q4_exception_tracking: '',
    q5_systems_count: '',
    q6_system_coordination: '',
    q7_rework_frequency: '',
    q8_insight_latency: '',
  })

  const handleContinue = (data: typeof assessmentData) => {
    setAssessmentData(data)
    const scoringResult = scoreAnswers(data)
    try {
      localStorage.setItem(`assessment_${sessionId}`, JSON.stringify({ sessionId, answers: data }))
    } catch {
      // Ignore storage errors for now.
    }
    notifyComplete({
      sessionId,
      scores: {
        index: scoringResult.index,
        operationalDrag: scoringResult.dimensions.operationalDrag.raw,
        manualControls: scoringResult.dimensions.manualControls.raw,
        fragmentation: scoringResult.dimensions.fragmentation.raw,
        insightLatency: scoringResult.dimensions.insightLatency.raw,
      },
      overallSeverity: scoringResult.overallSeverity,
    })
    if (window.__FINANCIAL_DRAG_RESULTS_URL__) {
      const target = new URL(window.__FINANCIAL_DRAG_RESULTS_URL__, window.location.origin)
      target.searchParams.set('session', sessionId)
      window.location.href = target.toString()
      return
    }
    setLocation(`/results?session=${sessionId}`)
  }

  return (
    <AssessmentScreen
      initialData={assessmentData}
      onContinue={handleContinue}
      onBack={() => setLocation('/')}
      currentStep={1}
    />
  )
}
