import { useEffect, useState } from 'react'
import { useLocation } from 'wouter'
import AssessmentResults from '@/components/assessment-results'

export default function Results() {
  const [location, setLocation] = useLocation()
  const [assessmentData, setAssessmentData] = useState({
    q1_operational_drag: '',
    q2_delay_cause: '',
    q3_manual_percentage: '',
    q4_exception_tracking: '',
    q5_systems_count: '',
    q6_system_coordination: '',
    q7_rework_frequency: '',
    q8_insight_latency: '',
  })

  useEffect(() => {
    const params = new URLSearchParams(window.location.search)
    let sessionId = params.get('session')

    if (!sessionId) {
      const root = document.getElementById('financial-drag-root')
      sessionId = root?.getAttribute('data-session-id') || null
    }

    if (!sessionId) {
      return
    }

    try {
      const stored = localStorage.getItem(`assessment_${sessionId}`)
      if (stored) {
        const parsed = JSON.parse(stored)
        if (parsed && parsed.answers) {
          setAssessmentData(parsed.answers)
        }
      }
    } catch {
      // Ignore storage errors for now.
    }
  }, [location])

  return <AssessmentResults assessmentData={assessmentData} onBack={() => setLocation('/assessment')} currentStep={3} />
}
