import { friNarrativeModel } from '@/config/friNarrativeModel'
import { ScoringResult } from '@/utils/friScoringEngine'

export function generateNarrative(scoringResult: ScoringResult): string {
  const { dimensions, overallSeverity } = scoringResult

  const overallText = friNarrativeModel.overallSeverity[overallSeverity]

  const dimensionRankings = [
    { name: 'operationalDrag', score: dimensions.operationalDrag.raw, level: dimensions.operationalDrag.level },
    { name: 'manualControls', score: dimensions.manualControls.raw, level: dimensions.manualControls.level },
    { name: 'fragmentation', score: dimensions.fragmentation.raw, level: dimensions.fragmentation.level },
    { name: 'insightLatency', score: dimensions.insightLatency.raw, level: dimensions.insightLatency.level },
  ]

  dimensionRankings.sort((a, b) => b.score - a.score)

  const topDimension = dimensionRankings[0]
  const secondDimension = dimensionRankings[1]

  const dimensionNames: Record<string, string> = {
    operationalDrag: 'operational drag',
    manualControls: 'manual controls',
    fragmentation: 'system fragmentation',
    insightLatency: 'insight latency',
  }

  const topDimensionDesc =
    friNarrativeModel.dimensionDescriptions[topDimension.name][topDimension.level]
  const secondDimensionDesc =
    friNarrativeModel.dimensionDescriptions[secondDimension.name][secondDimension.level]

  const narrative =
    `${overallText} ` +
    `The most significant friction occurs in ${dimensionNames[topDimension.name]}, where ${topDimensionDesc} ` +
    `Additional friction is seen in ${dimensionNames[secondDimension.name]}, where ${secondDimensionDesc} ` +
    `Together, this pattern slows the close, increases effort and elevates operational risk.`

  return narrative
}

export function generateOneLiners(
  scoringResult: ScoringResult,
): Record<string, string> {
  const { dimensions } = scoringResult

  return {
    operationalDrag: friNarrativeModel.oneLiners.operationalDrag[dimensions.operationalDrag.level],
    manualControls: friNarrativeModel.oneLiners.manualControls[dimensions.manualControls.level],
    fragmentation: friNarrativeModel.oneLiners.fragmentation[dimensions.fragmentation.level],
    insightLatency: friNarrativeModel.oneLiners.insightLatency[dimensions.insightLatency.level],
  }
}

export function generateImplications(scoringResult: ScoringResult): string[] {
  const { dimensions } = scoringResult

  const dimensionRankings = [
    { name: 'operationalDrag', score: dimensions.operationalDrag.raw, level: dimensions.operationalDrag.level },
    { name: 'manualControls', score: dimensions.manualControls.raw, level: dimensions.manualControls.level },
    { name: 'fragmentation', score: dimensions.fragmentation.raw, level: dimensions.fragmentation.level },
    { name: 'insightLatency', score: dimensions.insightLatency.raw, level: dimensions.insightLatency.level },
  ]

  dimensionRankings.sort((a, b) => b.score - a.score)

  const worstDimensions = dimensionRankings.slice(0, 3).filter((d) => {
    const levelScore =
      d.level === 'low' ? 1 : d.level === 'moderate' ? 2 : d.level === 'high' ? 3 : 4
    return levelScore >= 2
  })

  const bullets: string[] = []

  worstDimensions.forEach((dimension) => {
    const dimensionBullets = friNarrativeModel.implications[dimension.name][dimension.level]
    if (dimensionBullets) {
      bullets.push(...dimensionBullets)
    }
  })

  return bullets.slice(0, 4)
}

export function getEvidenceByDimension(scoringResult: ScoringResult): Record<string, string[]> {
  const { dimensions } = scoringResult
  const evidence: Record<string, string[]> = {}

  const dimensionsToInclude = [
    {
      name: 'operationalDrag',
      level: dimensions.operationalDrag.level,
    },
    {
      name: 'manualControls',
      level: dimensions.manualControls.level,
    },
    {
      name: 'fragmentation',
      level: dimensions.fragmentation.level,
    },
    {
      name: 'insightLatency',
      level: dimensions.insightLatency.level,
    },
  ]

  dimensionsToInclude.forEach((dimension) => {
    const levelScore = dimension.level === 'low' ? 1 : dimension.level === 'moderate' ? 2 : dimension.level === 'high' ? 3 : 4

    if (levelScore >= 3) {
      evidence[dimension.name] = friNarrativeModel.evidence[dimension.name]
    }
  })

  return evidence
}

export function getAllEvidenceSources(scoringResult: ScoringResult): string[] {
  const evidenceMap = getEvidenceByDimension(scoringResult)
  const allSources: string[] = []

  Object.values(evidenceMap).forEach((sources) => {
    allSources.push(...sources)
  })

  return Array.from(new Set(allSources))
}

export function getFirstEvidenceSnippet(dimension: string): string {
  const sources = friNarrativeModel.evidence[dimension]
  return sources && sources.length > 0 ? sources[0] : 'Evidence source will appear here.'
}
