import { Button } from '@/components/ui/button'
import { ArrowLeft, ChevronDown, ChevronUp, TrendingUp, CheckCircle, XCircle, Zap } from 'lucide-react'
import { useState, useMemo } from 'react'
import RadarChartPlaceholder from '@/components/RadarChartPlaceholder'
import { scoreAnswers } from '@/utils/friScoringEngine'
import {
  generateNarrative,
  generateOneLiners,
  generateImplications,
  getFirstEvidenceSnippet,
  getAllEvidenceSources,
} from '@/utils/friNarrativeEngine'
import { exportResultsPDF } from '@/utils/pdfExporter'

interface AssessmentResultsProps {
  onBack: () => void
  assessmentData: any
  currentStep?: number
}

export default function AssessmentResults({ onBack, assessmentData }: AssessmentResultsProps) {
  const automationComparisonImage = new URL('../assets/chart.png', import.meta.url).href
  const [isMethodologyExpanded, setIsMethodologyExpanded] = useState(false)

  const hasValidData = assessmentData && Object.values(assessmentData).some((val) => val !== '')

  const scoringResult = useMemo(() => {
    if (!hasValidData) {
      return {
        index: 0,
        overallSeverity: 'low',
        dimensions: {
          operationalDrag: { raw: 0, norm: 0, level: 'low' },
          manualControls: { raw: 0, norm: 0, level: 'low' },
          fragmentation: { raw: 0, norm: 0, level: 'low' },
          insightLatency: { raw: 0, norm: 0, level: 'low' },
        },
      }
    }
    return scoreAnswers(assessmentData)
  }, [assessmentData, hasValidData])

  const narrativeResult = useMemo(() => {
    if (!scoringResult) return null
    try {
      return {
        narrative: generateNarrative(scoringResult),
        oneLiners: generateOneLiners(scoringResult),
        implications: generateImplications(scoringResult),
        evidenceSources: getAllEvidenceSources(scoringResult),
      }
    } catch {
      return {
        narrative: 'Unable to generate narrative.',
        oneLiners: {
          operationalDrag: 'Unable to generate insight',
          manualControls: 'Unable to generate insight',
          fragmentation: 'Unable to generate insight',
          insightLatency: 'Unable to generate insight',
        },
        implications: [],
        evidenceSources: [],
      }
    }
  }, [scoringResult])

  return (
    <div id="fri-results-page" className="min-h-screen bg-white">
      <div className="border-b bg-white/80 backdrop-blur-md sticky top-0 z-50 shadow-sm">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <Button
            onClick={onBack}
            className="gap-2 hover:bg-gray-100 transition-colors"
          >
            <ArrowLeft className="h-4 w-4" />
            Back
          </Button>
        </div>
      </div>

      <div className="max-w-[920px] mx-auto px-6 py-20 space-y-20">
        <div className="text-center space-y-8">
          <div className="space-y-4">
            <h1 className="text-4xl md:text-5xl font-bold text-slate-900 tracking-tight">
              Your close friction index
            </h1>
            <p className="text-lg text-slate-600 max-w-2xl mx-auto">
              A research-backed view of friction in your close process
            </p>
          </div>

          <div className="inline-flex flex-col items-center gap-6">
            <div className="relative">
              <div className="absolute inset-0 bg-gradient-to-br from-slate-100 to-slate-200 blur-3xl opacity-50 rounded-full" />
              <div className="relative bg-white border-4 border-slate-200 rounded-3xl px-16 py-12 shadow-2xl">
                <div className="text-8xl font-bold text-slate-900 tracking-tight">
                  {scoringResult.index}
                </div>
                <div className="text-sm font-semibold text-slate-500 uppercase tracking-widest mt-2">
                  Out of 100
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="space-y-12">
          <RadarChartPlaceholder dimensions={scoringResult.dimensions} />

          <div className="bg-gradient-to-br from-teal-50 to-cyan-50 border-2 border-teal-300 rounded-3xl p-12 shadow-lg">
            <p className="text-2xl font-bold text-slate-900 mb-6">
              Your close process experiences{' '}
              <span className="text-teal-700">
                {scoringResult.overallSeverity.charAt(0).toUpperCase() + scoringResult.overallSeverity.slice(1)}
              </span>{' '}
              friction
            </p>
            <p className="text-lg leading-relaxed text-slate-700">
              {narrativeResult?.narrative || 'Executive summary narrative will be dynamically generated here.'}
            </p>
          </div>
        </div>

        <div className="space-y-8">
          <div className="bg-white border-2 border-slate-200 rounded-2xl p-8 space-y-6">
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <h3 className="text-xl font-semibold text-slate-900">Operational drag</h3>
                <span className="text-lg font-bold text-slate-700">
                  Score: {scoringResult.dimensions.operationalDrag.raw} (normalised {scoringResult.dimensions.operationalDrag.norm})
                </span>
              </div>
              <p className="text-base text-slate-600 leading-relaxed">
                {narrativeResult?.oneLiners.operationalDrag || 'One-liner insight will be inserted dynamically.'}
              </p>
              <p className="text-sm text-slate-500 italic">
                Evidence: {getFirstEvidenceSnippet('operationalDrag')}
              </p>
            </div>
          </div>

          <div className="bg-white border-2 border-slate-200 rounded-2xl p-8 space-y-6">
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <h3 className="text-xl font-semibold text-slate-900">Manual controls</h3>
                <span className="text-lg font-bold text-slate-700">
                  Score: {scoringResult.dimensions.manualControls.raw} (normalised {scoringResult.dimensions.manualControls.norm})
                </span>
              </div>
              <p className="text-base text-slate-600 leading-relaxed">
                {narrativeResult?.oneLiners.manualControls || 'One-liner insight will be inserted dynamically.'}
              </p>
              <p className="text-sm text-slate-500 italic">
                Evidence: {getFirstEvidenceSnippet('manualControls')}
              </p>
            </div>
          </div>

          <div className="bg-white border-2 border-slate-200 rounded-2xl p-8 space-y-6">
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <h3 className="text-xl font-semibold text-slate-900">Fragmentation</h3>
                <span className="text-lg font-bold text-slate-700">
                  Score: {scoringResult.dimensions.fragmentation.raw} (normalised {scoringResult.dimensions.fragmentation.norm})
                </span>
              </div>
              <p className="text-base text-slate-600 leading-relaxed">
                {narrativeResult?.oneLiners.fragmentation || 'One-liner insight will be inserted dynamically.'}
              </p>
              <p className="text-sm text-slate-500 italic">
                Evidence: {getFirstEvidenceSnippet('fragmentation')}
              </p>
            </div>
          </div>

          <div className="bg-white border-2 border-slate-200 rounded-2xl p-8 space-y-6">
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <h3 className="text-xl font-semibold text-slate-900">Insight latency</h3>
                <span className="text-lg font-bold text-slate-700">
                  Score: {scoringResult.dimensions.insightLatency.raw} (normalised {scoringResult.dimensions.insightLatency.norm})
                </span>
              </div>
              <p className="text-base text-slate-600 leading-relaxed">
                {narrativeResult?.oneLiners.insightLatency || 'One-liner insight will be inserted dynamically.'}
              </p>
              <p className="text-sm text-slate-500 italic">
                Evidence: {getFirstEvidenceSnippet('insightLatency')}
              </p>
            </div>
          </div>
        </div>

        <div className="bg-white border-2 border-slate-200 rounded-2xl p-10 space-y-6">
          <h2 className="text-2xl font-bold text-slate-900">
            What this means for your organisation
          </h2>

          <div className="space-y-4">
            {narrativeResult?.implications && narrativeResult.implications.length > 0 ? (
              narrativeResult.implications.map((implication, index) => (
                <div key={index} className="flex items-start gap-4">
                  <div className="p-2 bg-slate-100 rounded-lg mt-0.5">
                    <div className="w-2 h-2 bg-slate-500 rounded-full" />
                  </div>
                  <div className="flex-1">
                    <p className="text-slate-700 leading-relaxed">
                      {implication}
                    </p>
                  </div>
                </div>
              ))
            ) : (
              <>
                <div className="flex items-start gap-4">
                  <div className="p-2 bg-slate-100 rounded-lg mt-0.5">
                    <div className="w-2 h-2 bg-slate-500 rounded-full" />
                  </div>
                  <div className="flex-1">
                    <p className="text-slate-700 leading-relaxed">
                      Insight bullet placeholder #1
                    </p>
                  </div>
                </div>

                <div className="flex items-start gap-4">
                  <div className="p-2 bg-slate-100 rounded-lg mt-0.5">
                    <div className="w-2 h-2 bg-slate-500 rounded-full" />
                  </div>
                  <div className="flex-1">
                    <p className="text-slate-700 leading-relaxed">
                      Insight bullet placeholder #2
                    </p>
                  </div>
                </div>
              </>
            )}
          </div>
        </div>

        <div className="space-y-4">
          <button
            onClick={() => setIsMethodologyExpanded(!isMethodologyExpanded)}
            className="text-slate-600 hover:text-slate-900 text-sm font-medium flex items-center gap-2 transition-colors"
          >
            View methodology and sources
            {isMethodologyExpanded ? <ChevronUp className="h-4 w-4" /> : <ChevronDown className="h-4 w-4" />}
          </button>

          {isMethodologyExpanded && (
            <div className="bg-slate-50 border border-slate-200 rounded-2xl p-8 space-y-6 animate-in fade-in slide-in-from-top-4 duration-300">
              <div className="space-y-4">
                {narrativeResult?.evidenceSources && narrativeResult.evidenceSources.length > 0 ? (
                  <>
                    <p className="text-base text-slate-700">
                      Evidence sources supporting your assessment:
                    </p>
                    <ul className="space-y-3 list-disc list-inside text-slate-600">
                      {narrativeResult.evidenceSources.map((source, index) => (
                        <li key={index}>{source}</li>
                      ))}
                    </ul>
                  </>
                ) : (
                  <>
                    <p className="text-base text-slate-700">
                      Evidence sources will be inserted here, including:
                    </p>
                    <ul className="space-y-3 list-disc list-inside text-slate-600">
                      <li>EY cycle-time study</li>
                      <li>PwC State of Controls</li>
                      <li>Gartner R2R fragmentation analysis</li>
                      <li>Hackett insight-latency research</li>
                    </ul>
                  </>
                )}
              </div>
            </div>
          )}
        </div>

        <div className="space-y-8">
          <div className="text-center space-y-4">
            <h2 className="text-3xl font-bold text-slate-900 tracking-tight">
              Where your friction is highest, Redwood delivers the most impact.
            </h2>
            <p className="text-lg text-slate-600 max-w-3xl mx-auto leading-relaxed">
              How Redwood transforms R2R performance
            </p>
          </div>

          <div className="bg-white border-2 border-slate-200 rounded-3xl p-12 shadow-xl">
            <div className="text-center mb-12">
              <h3 className="text-4xl font-bold mb-4 text-slate-900">
                Redwood offers much higher levels of R2R automation
              </h3>
              <p className="text-xl text-slate-600 max-w-4xl mx-auto">
                Comprehensive automation comparison across Record-to-Report processes
              </p>
            </div>

            <div className="mb-12 rounded-2xl overflow-hidden border-2 border-slate-600/20 shadow-lg">
              <img
                src={automationComparisonImage}
                alt="R2R landscape showing Redwood platform offers much higher levels of automation"
                className="w-full h-auto"
              />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
              {[
                { value: '80%', label: 'Task Management', sublabel: 'Redwood Automation' },
                { value: '70%', label: 'Journals & Accruals', sublabel: 'Redwood Automation' },
                { value: '80%', label: 'Intercompany', sublabel: 'Redwood Automation' },
                { value: '75%', label: 'Account Reconciliation', sublabel: 'Redwood Automation' },
              ].map((item, index) => (
                <div key={index} className="text-center p-6 bg-slate-600/10 rounded-2xl border-2 border-slate-600/20">
                  <div className="text-4xl font-bold text-slate-600 mb-2">{item.value}</div>
                  <div className="text-base font-medium text-slate-900 mb-1">{item.label}</div>
                  <div className="text-sm text-slate-700">{item.sublabel}</div>
                </div>
              ))}
            </div>

            <div className="text-center">
              <div className="inline-flex items-center gap-3 bg-slate-600/15 text-slate-700 px-8 py-4 rounded-2xl border-2 border-slate-600/40">
                <TrendingUp className="h-6 w-6" />
                <span className="font-bold text-lg">Average 76% automation vs 22.5% for non-Redwood solutions</span>
              </div>
            </div>
          </div>

          <div className="bg-white border-2 border-slate-200 rounded-3xl p-12 shadow-xl">
            <div className="flex items-center gap-4 mb-12">
              <div className="p-3 bg-slate-600/10 rounded-xl border border-slate-600/20">
                <Zap className="h-8 w-8 text-slate-600" />
              </div>
              <div>
                <h3 className="text-4xl font-bold text-slate-900">
                  R2R Process Transformation Example
                </h3>
                <p className="text-xl text-slate-600">
                  Account reconciliation workflow: Before vs After Redwood
                </p>
              </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-5 gap-8">
              <div className="lg:col-span-2 bg-gray-50 rounded-2xl p-8 border-2 border-gray-200">
                <div className="flex items-center justify-center gap-4 text-gray-900 mb-8">
                  <XCircle className="h-8 w-8 text-gray-600" />
                  <h4 className="text-lg font-semibold">Before Redwood</h4>
                </div>

                <div className="space-y-4">
                  <div className="text-sm font-medium text-gray-700 mb-3">
                    Data Collection (70% of Effort)
                  </div>
                  <div className="space-y-3">
                    {[
                      { task: 'Download open GL items', tool: 'SAP', manual: true },
                      { task: 'Calculate Ageing at line item level', tool: 'Excel', manual: true },
                      { task: 'Browse through Text and update Category', tool: 'Excel', manual: true },
                    ].map((item, i) => (
                      <div
                        key={i}
                        className="flex items-center gap-3 p-3 bg-white rounded-lg border border-gray-300"
                      >
                        <div className="p-1 bg-gray-100 rounded">
                          <svg
                            className="h-4 w-4 text-gray-600"
                            fill="currentColor"
                            viewBox="0 0 20 20"
                          >
                            <path
                              fillRule="evenodd"
                              d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                              clipRule="evenodd"
                            />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="text-sm font-medium text-gray-900">{item.task}</div>
                          <div className="text-xs text-gray-500">{item.tool}</div>
                        </div>
                      </div>
                    ))}
                  </div>

                  <div className="text-sm font-medium text-gray-700 mb-3 mt-6">
                    Commentary (20% of Effort)
                  </div>
                  <div className="space-y-3">
                    {[
                      {
                        task: 'Collect and attach supporting doc',
                        tool: 'Account Reconciliation Tool Providers',
                        manual: true,
                      },
                      {
                        task: 'Send for approval and incorporate comments',
                        tool: 'Account Reconciliation Tool Providers',
                        manual: true,
                      },
                    ].map((item, i) => (
                      <div
                        key={i}
                        className="flex items-center gap-3 p-3 bg-white rounded-lg border border-gray-300"
                      >
                        <div className="p-1 bg-gray-100 rounded">
                          <svg
                            className="h-4 w-4 text-gray-600"
                            fill="currentColor"
                            viewBox="0 0 20 20"
                          >
                            <path
                              fillRule="evenodd"
                              d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                              clipRule="evenodd"
                            />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="text-sm font-medium text-gray-900">{item.task}</div>
                          <div className="text-xs text-gray-500">{item.tool}</div>
                        </div>
                      </div>
                    ))}
                  </div>

                  <div className="text-sm font-medium text-gray-700 mb-3 mt-6">
                    Submission (10% of Effort)
                  </div>
                  <div className="space-y-3">
                    <div className="flex items-center gap-3 p-3 bg-white rounded-lg border border-gray-300">
                      <div className="p-1 bg-gray-100 rounded">
                        <svg
                          className="h-4 w-4 text-gray-600"
                          fill="currentColor"
                          viewBox="0 0 20 20"
                        >
                          <path
                            fillRule="evenodd"
                            d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                            clipRule="evenodd"
                          />
                        </svg>
                      </div>
                      <div className="flex-1">
                        <div className="text-sm font-medium text-gray-900">Archive</div>
                        <div className="text-xs text-gray-500">
                          Account Reconciliation Tool Providers
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="lg:col-span-3 bg-slate-800 rounded-2xl p-8 border-2 border-slate-600 text-white shadow-2xl">
                <div className="flex items-center justify-center gap-4 text-white mb-8">
                  <CheckCircle className="h-8 w-8 text-white" />
                  <h4 className="text-lg font-semibold text-white">After Redwood</h4>
                </div>

                <div className="space-y-4">
                  <div className="text-sm font-medium text-gray-200 mb-3">Automated Data Processing</div>
                  <div className="space-y-3">
                    {[
                      { task: 'Download open GL items', automated: true },
                      { task: 'Update Classification and Ageing at line item levels', automated: true },
                      { task: 'Update Summary Sheet', automated: true },
                      { task: 'Create & attach Supporting doc', automated: true },
                    ].map((item, i) => (
                      <div
                        key={i}
                        className="flex items-center gap-3 p-3 bg-slate-700/80 rounded-lg border border-slate-600 shadow-sm"
                      >
                        <div className="p-1 bg-slate-600/40 rounded">
                          <svg className="h-4 w-4 text-white" fill="currentColor" viewBox="0 0 20 20">
                            <path
                              fillRule="evenodd"
                              d="M11.49 3.17c-.38-1.56-2.6-1.56-2.98 0a1.532 1.532 0 01-2.286.948c-1.372-.836-2.942.734-2.106 2.106.54.886.061 2.042-.947 2.287-1.561.379-1.561 2.6 0 2.978a1.532 1.532 0 01.947 2.287c-.836 1.372.734 2.942 2.106 2.106a1.532 1.532 0 012.287.947c.379 1.561 2.6 1.561 2.978 0a1.533 1.533 0 012.287-.947c1.372.836 2.942-.734 2.106-2.106a1.533 1.533 0 01.947-2.287c1.561-.379 1.561-2.6 0-2.978a1.532 1.532 0 01-.947-2.287c.836-1.372-.734-2.942-2.106-2.106a1.532 1.532 0 01-2.287-.947zM10 13a3 3 0 100-6 3 3 0 000 6z"
                              clipRule="evenodd"
                            />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="text-sm font-medium text-white">{item.task}</div>
                          <div className="text-xs text-gray-300">Automated</div>
                        </div>
                        <div className="p-1 bg-emerald-900/40 rounded">
                          <svg className="h-3 w-3 text-slate-300" fill="currentColor" viewBox="0 0 20 20">
                            <path
                              fillRule="evenodd"
                              d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 01-1.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                              clipRule="evenodd"
                            />
                          </svg>
                        </div>
                      </div>
                    ))}
                  </div>

                  <div className="text-sm font-medium text-gray-200 mb-3 mt-6">Minimal Manual Review</div>
                  <div className="space-y-3">
                    <div className="flex items-center gap-3 p-3 bg-slate-700/80 rounded-lg border border-slate-600 shadow-sm">
                      <div className="p-1 bg-amber-500/40 rounded">
                        <svg className="h-4 w-4 text-amber-200" fill="currentColor" viewBox="0 0 20 20">
                          <path
                            fillRule="evenodd"
                            d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                            clipRule="evenodd"
                          />
                        </svg>
                      </div>
                      <div className="flex-1">
                        <div className="text-sm font-medium text-white">Review and update comments</div>
                        <div className="text-xs text-gray-300">Manual oversight</div>
                      </div>
                    </div>
                  </div>

                  <div className="text-sm font-medium text-gray-200 mb-3 mt-6">Automated Workflow</div>
                  <div className="space-y-3">
                    {[
                      { task: 'Route for approval', automated: true },
                      { task: 'Archive', automated: true },
                    ].map((item, i) => (
                      <div
                        key={i}
                        className="flex items-center gap-3 p-3 bg-slate-700/80 rounded-lg border border-slate-600 shadow-sm"
                      >
                        <div className="p-1 bg-slate-600/40 rounded">
                          <svg className="h-4 w-4 text-white" fill="currentColor" viewBox="0 0 20 20">
                            <path
                              fillRule="evenodd"
                              d="M11.49 3.17c-.38-1.56-2.6-1.56-2.98 0a1.532 1.532 0 01-2.286.948c-1.372-.836-2.942.734-2.106 2.106.54.886.061 2.042-.947 2.287-1.561.379-1.561 2.6 0 2.978a1.532 1.532 0 01.947 2.287c-.836 1.372.734 2.942 2.106 2.106a1.532 1.532 0 012.287.947c.379 1.561 2.6 1.561 2.978 0a1.533 1.533 0 012.287-.947c1.372.836 2.942-.734 2.106-2.106a1.533 1.533 0 01.947-2.287c1.561-.379 1.561-2.6 0-2.978a1.532 1.532 0 01-.947-2.287c.836-1.372-.734-2.942-2.106-2.106a1.532 1.532 0 01-2.287-.947zM10 13a3 3 0 100-6 3 3 0 000 6z"
                              clipRule="evenodd"
                            />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="text-sm font-medium text-white">{item.task}</div>
                          <div className="text-xs text-gray-300">Automated</div>
                        </div>
                        <div className="p-1 bg-emerald-900/40 rounded">
                          <svg className="h-3 w-3 text-slate-300" fill="currentColor" viewBox="0 0 20 20">
                            <path
                              fillRule="evenodd"
                              d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 01-1.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                              clipRule="evenodd"
                            />
                          </svg>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            <div className="mt-12 text-center">
              <div className="bg-white border-2 border-slate-600/30 rounded-2xl p-8 shadow-lg max-w-2xl mx-auto">
                <div className="flex items-center justify-center gap-4 text-slate-600">
                  <Zap className="h-8 w-8" />
                  <span className="font-bold text-2xl">90% reduction in manual effort  75% faster processing</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="text-center pt-12 pb-8 space-y-6">
          <div className="space-y-3">
            <h2 className="text-2xl font-bold text-slate-900">Get your full diagnostic report</h2>
            <p className="text-lg text-slate-600 max-w-2xl mx-auto">
              Your personalised score, detailed explanations and a tailored improvement plan.
            </p>
          </div>

          <Button
            onClick={() => exportResultsPDF({ scoringResult, narrativeResult })}
            className="bg-slate-600 hover:bg-slate-700 text-white px-16 py-7 text-lg font-semibold rounded-xl shadow-2xl hover:shadow-3xl transition-all duration-300 hover:-translate-y-1"
          >
            Download your full diagnostic
          </Button>
        </div>
      </div>
    </div>
  )
}
